@use "global" as *;

.contact {
  filter: drop-shadow(0 rem(4) rem(4) rgba($color: $black2, $alpha: 0.25));
  @include mq("md"){
    filter: none;
  }
}

.contact__inner{
  position: relative;
}

.contact__contents {
  @include mq("md"){
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: rem(4) rem(4) rem(10) rgba($color: $black2, $alpha: 0.25);
    padding: rem(80) rem(40) rem(100);
    position: relative;
  }
}

.contact__contents::before{
  @include mq("md"){
    content: "";
    position: absolute;
    width: rem(1);
    height: rem(255);
    top: 50%;
    left: 53%;
    transform: translateY(-50%);
    border-radius: rem(5);
    background-color: $green;
  }
}

.contact__access {
  padding-right: rem(10);
  padding-left: rem(10);
  margin-right: auto;
  margin-left: auto;
  @include mq("md"){
    width: 50%;
  }
}

.contact__site-logo {
  width: 100%;
  position: relative;
}

.contact__site-logo::before{
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: rem(-10);
  width: 100%;
  height: rem(1);
  border-radius: rem(5);
  background: $green;
}

.contact__site-logo img{
  margin: 0 auto;
  width: rem(174);
  @include mq("md"){
    width: rem(200);
  }
}

.contact__access-box {
  margin-top: rem(22);
  @include mq("md"){
    display: flex;
    justify-content: space-between;
    margin-top: rem(30);
  }
}

.contact__access-list{
  @include mq("md"){
  width: 40%;
  }
}

address .contact__access-text {
  text-align: center;
  font-style: normal;
  font-size: rem(16);
  line-height: 1.5;
    @include mq("md"){
      text-align: left;
    }
}

.contact__access-text:not(:first-child){
  margin-top: rem(4);
}

.contact__access-map-wrap{
  margin-top: 1rem;
  width: 100%;
  @include mq("md"){
    margin-top: 0;
    margin-left: rem(20);
    max-width: rem(273);
    width: 60%;
  }
}

.contact__access-map {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 47.5%;
}

.contact__access-map iframe{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.contact__guidance {
  margin-top: rem(60);
  @include mq("md"){
    margin-top: rem(20);
    width: 50%;
    margin-left: rem(80);
  }
}

.contact__text {
  font-size: rem(14);
  line-height: calc(18/14);
  color: $green;
  text-align: center;
  margin-top: rem(8);
  @include mq("md"){
    margin-top: rem(4);
  }
}

.contact__button-wrap {
  margin-top: rem(40);
  text-align: center;
}