@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP",sans-serif;
$second-font-family:"Gotu",sans-serif;
$third-font-family: "Lato",sans-serif;

// ※検証ツールでしっかり反映されているか確認する


body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 300;
$normal: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #0D2936;
$black2: #000000;
$black3: #20202020;
$white: #fff;
$green: #408F95;
$green2: #DDF0F1;
$red: #C94800;
$gray: #D9D9D9;
$gray2: #9A9A9A;

