@use "global" as *;

.about-us__inner{
  @include mq("md"){
    position: relative;
  }
}

.about-us__container{
  margin-top: rem(30);
  position: relative;
  @include mq("md"){
    margin-top: rem(40);
  }
}

.about-us__img-box{
  position: relative;
}

.about-us__right-wrap {
  padding-top: rem(25);
  width: 81%;
  min-width: rem(281);
  margin-left: auto;
  @include mq("md"){
    padding-top: rem(80);
    width: 100%;
    max-width: rem(880);
  }
}


.about-us__right-wrap img{
  aspect-ratio: 281/186;
  object-fit: cover;
  @include mq("md"){
    aspect-ratio: 880/581;
  }
}


.about-us__left-wrap {
  position: absolute;
  top: 0;
  left: 0;
  min-width: rem(128);
  width: 37%;
  @include mq("md"){
    width: 100%;
    max-width: rem(400);
  }
}

.about-us__left-wrap img{
  aspect-ratio: 400/600;
  object-fit: cover;
}

.about-us__contents {
  margin-top: rem(42);
  @include mq("md"){
    margin-top: 0;
    position: absolute;
    top: 48.3%;
    left: 18.5%;
    transform: translateY(-50%);
    display: flex;
  }
}

.about-us__copy {
  color: $green;
  font-size: rem(40);
  line-height: 1;
  font-family: $second-font-family;
  @include mq("md"){
    color: $white;
    font-size: rem(80);
    line-height: 1.25;
  }
}

.about-us__text-box {
  margin-top: rem(16);
  @include mq("md"){
    margin-top: 0;
    margin-left: rem(40);
    width: 45%;
  }
}

.about-us__text {
  line-height: 1.5;
  @include mq("md"){
    color: $white;
  }
}

.about-us__button-wrapper {
  margin-top: rem(50);
  text-align: center;
  @include mq("md"){
    margin-top: rem(40);
  }
}

.about-us__under-img-wrap{
  width: 100%;
  max-width: rem(194);
  position: absolute;
  bottom: -11%;
  right: calc(50% - min(48vw, rem(637)));
  z-index: 5;
}
