@use "global" as *;

.info{
  background-color: $green2;
  padding-top: rem(80);
  padding-bottom: rem(80);
  @include mq("md"){
    padding-top: rem(102);
    padding-bottom: rem(76);
  }
}

.info__inner{
  max-width: rem(600);
}

.info__contents{
  margin-top: rem(29);
  @include mq("md"){
    margin-top: rem(40);
    display: flex;
  }
}

.info__sub-title{
  font-size: rem(20);
  font-weight: $bold;
  line-height: 2;
  position: relative;
  @include mq("md"){
    font-size: rem(24);
    line-height: calc(24/24);
  }
}

.info__sub-title::before{
  content: "";
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: rem(-4);
  width: 100%;
  height: rem(1);
  border-radius: rem(5);
  background: $green;
  @include mq("md"){
    bottom: rem(-10);
  }
}

.info__img-wrap{
  width: 100%;
  @include mq("md"){
    width: 50%;
  }
}

.info__img-wrap img{
  aspect-ratio: 1080/712;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.info__text-content{
  margin-top: rem(24);
  @include mq("md"){
    margin-top: rem(61);
    margin-left: rem(47);
    width: 45%;
  }
}

.info__text{
  margin-top: rem(13);
  font-size: rem(16);
  line-height: calc(24/16);
  @include mq("md"){
    margin-top: rem(18);
  }
}

.info__button-wrap{
  margin-top: rem(40);
  text-align: center;
}
